@import url(https://fonts.googleapis.com/css?family=Roboto:500);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Make clicks pass-through */
#nprogress {
    pointer-events: none;
  }
  
  #nprogress .bar {
    background: #29d;
  
    position: fixed;
    z-index: 1031;
    top: 0;
    left: 0;
  
    width: 100%;
    height: 2px;
  }
  
  /* Fancy blur effect */
  #nprogress .peg {
    display: block;
    position: absolute;
    right: 0px;
    width: 100px;
    height: 100%;
    box-shadow: 0 0 10px #29d, 0 0 5px #29d;
    opacity: 1.0;
  
    -webkit-transform: rotate(3deg) translate(0px, -4px);
            transform: rotate(3deg) translate(0px, -4px);
  }
  
  /* Remove these to get rid of the spinner */
  #nprogress .spinner {
    display: block;
    position: fixed;
    z-index: 1031;
    top: 15px;
    right: 15px;
  }
  
  #nprogress .spinner-icon {
    width: 18px;
    height: 18px;
    box-sizing: border-box;
  
    border: solid 2px transparent;
    border-top-color: #29d;
    border-left-color: #29d;
    border-radius: 50%;
  
    -webkit-animation: nprogress-spinner 400ms linear infinite;
            animation: nprogress-spinner 400ms linear infinite;
  }
  
  .nprogress-custom-parent {
    overflow: hidden;
    position: relative;
  }
  
  .nprogress-custom-parent #nprogress .spinner,
  .nprogress-custom-parent #nprogress .bar {
    position: absolute;
  }
  
  @-webkit-keyframes nprogress-spinner {
    0%   { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  @keyframes nprogress-spinner {
    0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  
html, body {
  height: 100%;
}

.app {
  padding: 30px;
  background-color: #2d007a;
}

.heading {
  margin-bottom: 50px;
}

.heading, .search-heading {
  color: white;
  text-align: center;
}


.event {
    margin-bottom: 30px;
    opacity: .9;
}

.event-body {
    border-radius: 20px;
    text-align: left;
}

.event-summary {
    padding-top: 30px;
    padding-bottom: 20px;
    text-align: center;
}

.event-location, .event-date, .event-start-time, .event-end-time {
    margin-left: 20px;
}

#toggle-details {
    margin-left: 20px;
    color: white;
    margin-bottom: 30px;
    border: none;
    background-color: #FAA916;
}

.event-details {
    margin: 20px;
    padding-bottom: 20px;
}
.col-xl-3, .col-md-6, .col-lg-4 {
    align-items: center;
}
ul {
  padding: 0;
  list-style: none;
}
  
  input[type="text"],
  input[type="number"] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    align-self: center;
    height: 38px;
  }
  
  .city {
    width: 250px;
  }
  
  .CitySearch {
    display: flex;
    flex-direction: column;
    flex-basis: 300px;
    max-width: 300px;
    margin: auto;
    position: relative;
    padding-bottom: 30px;
  }
  
  .suggestions {
    width: 250px;
    /* position: absolute; */
    z-index: 2;
    align-self: center;
    margin: 0;
    top: 43px;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
  }
  
  .suggestions > li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
  }
  
  .suggestions > li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  
  .suggestions > li:hover {
    background-color: #bcdcdb;
  }
.total-events-viewed {
    width: 200px;
    margin: 20px auto;
    text-align: center;
}

.number-of-events {
  color: white;
}

.numbers {
    max-width: 60vw;
}
